import React, { Fragment } from 'react';
import {
  H1,
  H3,
  Content,
  CodeArea,
  CodeComment,
  InfoText,
  CentralizedImg,
} from 'components/styles';
import Accordion from 'components/Accordion';
import { NavLinks, NextLink } from 'components/Links';

const WtfIsJs = () => (
  <Fragment>
    <H1>1. WTF is JS</H1>
    <Content>
      <InfoText>
        Este  o captulo inicial do minicurso de JavaScript, abordei a seguir algumas informaes e curiosidades sobre
        a linguagem.
      </InfoText>
      <Accordion title="1.1. Origem">
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0 }}>
            <b>Javascript</b>, ou simplesmente <b>JS</b>,  uma linguagem de programao de&nbsp;
            <a href="https://becode.com.br/linguagens-alto-nivel-x-baixo-nivel/">alto nvel</a> desenvolvida em meados
            de 1996 pelo programador <strike>Tom Hanks</strike> <b>Brendan Eich</b>, que foi posteriormente um dos
            fundadores da <b>Mozilla Corporation</b>. A linguagem foi desenvolvida no auge da <b>Netscape</b>, como uma
            soluo para rodar <b>scripts do lado do cliente</b> e <b>interagir a interaes de usurio</b>, eliminando
            a necessidade de pedir requisies para um servidor.
            <br /><br />
            Inicialmente desenvolvida para o <b>Netscape Navigator</b>, teve seu suporte extendido para outras
            plataformas e atualmente  a <b>linguagem mais popular para programao client-side.</b> Assim como o&nbsp;
            <b>
              <a href="https://www.hostinger.com.br/tutoriais/o-que-e-html-conceitos-basicos/">HTML</a> e o&nbsp;
              <a href="https://www.hostinger.com.br/tutoriais/o-que-e-css-guia-basico-de-css/">CSS</a>
            </b>, passou por mudanas cruciais e hoje  utilizada tambm para&nbsp;
            <b>cdigos do lado do servidor</b>.
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="brendan_eich"
              // eslint-disable-next-line
              src="https://cnet4.cbsistatic.com/img/gRItWJMKsaqeAKIOnSbD31qzwew=/570x0/2012/02/26/24cf0440-f0e6-11e2-8c7c-d4ae52e62bcc/20120226_Brendan_Eich_001.jpg"
              style={{
                height: 200,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0, paddingTop: 0 }}>
            <H3>Prottipo</H3>
            Para defender a <b>proposta</b> da linguagem em relao com outras j existentes e estreitar as relaes
            com a <b>Sun Microsystems (desenvolvedora do Java)</b>, a <b>Netscape</b> encarregou <b>Eich</b> para
            elaborar um prottipo.
            <br /><br />
            O prottipo, originalmente chamado de <b>Mocha</b> e lanado como <b>LiveScript</b> na sua verso Beta,
            foi feito em <b>apenas 10 dias!</b>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="netscape"
              // eslint-disable-next-line
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/66/Netscape_logo.svg/2000px-Netscape_logo.svg.png"
              style={{
                borderRadius: 0,
                height: 50,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingTop: 0 }}>
            <H3>No  Java!</H3>
            Seu nome pode sugerir uma similaridade a linguagem&nbsp;
            <a href="https://www.java.com/en/download/faq/whatis_java.xml">Java</a>, mas no se engane:&nbsp;
            <b>Javascript no tem nada a ver com Java!</b> De fato a confuso vem desde seu lanamento, j que a&nbsp;
            <b>Netscape</b> tambm desenvolvia na poca o suporte para <b>Java</b>.
            <br /><br />
            Seu nome final foi o resultado de uma <b>campanha de marketing</b> em conjunto com a&nbsp;
            <b>Sun Microsystems</b> para aproveitar a popularidade das linguagens (e lan-las em conjunto).
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="java"
              src="https://diylogodesigns.com/wp-content/uploads/2017/07/java-logo-vector-768x768.png"
              style={{
                borderRadius: 0,
                height: 150,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="1.2. Evoluo">
        1.2. Evoluo
        <H3>ECMAScript</H3>
        <H3>Multiparadigma</H3>
      </Accordion>
      <Accordion title="1.3. Aplicaes">
        1.3. Aplicaes
        <H3>Client-side</H3>
        <H3>Server-side</H3>
      </Accordion>
      <Accordion title="1.4. Vanilla X Frameworks">
        1.4. Vanilla X Frameworks
        <H3>jQuery</H3>
        <H3>Node</H3>
      </Accordion>
      <NavLinks>
        <NextLink to="/2-basics">2. Bsico</NextLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default WtfIsJs;



