import React, { Fragment } from 'react';
import {
  H1,
  H3,
  Content,
  CodeArea,
  CodeComment,
  InfoText,
  CentralizedImg,
} from 'components/styles';
import Accordion from 'components/Accordion';
import { NavLinks, PrevLink } from 'components/Links';

const Tools = () => (
  <Fragment>
    <H1>5. Ferramentas</H1>
    <Content>
      <InfoText>
        <strong>Apndice e literatura complementar.</strong> Deixo aqui algumas ferramentas que uso no dia a dia e
        que <strong>ajudam ou possibilitam o desenvolvimento</strong> usando frameworks e afins. So tecnologias&nbsp;
        <strong>muito utilizadas em cdigos de produo</strong> e permitem fazer coisas legais!
        Assim como em outras linguagens, <strong>no existe apenas uma forma correta</strong> de se fazer algo. De fato,
        o Javascript possui tantas <strong>estruturas e frameworks diferentes</strong> que isso encoraja tentar de
        diferentes formas. Como o <strong>&quot;ambiente web&quot;</strong> est em constante mudana, foquei nos
        principais pilares dos grandes projetos.&nbsp;
        <br /><br />
        <strong>
           possvel que essas ferramentas mudem com o tempo, tanto em sintaxe quanto em funcionamento!
        </strong>
        <CentralizedImg
          alt="hacking"
          src="https://media.giphy.com/media/eCqFYAVjjDksg/giphy.gif"
          style={{
            marginTop: '1em',
            height: 200,
          }}
        />
      </InfoText>
      <Accordion title="5.1. Node">
        <CodeArea>
          <CodeComment>
            <strong>Node</strong>, popularmente conhecido como <strong>node.js</strong>,  um&nbsp;
            <strong>ambiente de execuo</strong> para Javascript, construdo sobre a&nbsp;
            <strong>engine <a href="https://developers.google.com/v8/">V8</a> do Google Chrome.</strong> Focado no
            desenvolvimento de servidores, seus principais diferenciais so:
            <ul>
              <li>
                <strong>Entrada/sada orientada a eventos:</strong> cdigo responde a requisies assncronas de acordo
                com <strong>eventos de usurio</strong> (interaes, AJAX, aes sncronas)
              </li>
              <li>
                <strong>Escalabilidade:</strong> ao lidar com eventos, assume-se que&nbsp;
                <strong>no h execues bloqueantes</strong> e, ao contrrio do que acontece com outras
                plataformas, <strong>no cria uma tarefa nova</strong> a cada evento. Dessa forma, o Node lida com cada
                evento utilizando um&nbsp;
                <a href="https://itnext.io/multi-threading-and-multi-process-in-node-js-ffa5bb5cde98">
                  gerenciador prprio
                </a>
              </li>
              <li>
                <strong>Modularidade:</strong> apenas as funcionalidades <strong>bsicas</strong> so entregues pelo
                Node. Qualquer extenso da linguagem  feita atravs de <strong>mdulos e frameworks externos</strong>,
                que so gerenciados atravs de <strong>pacotes e dependncias (ver tpico NPM!)</strong>
              </li>
            </ul>
            <strong>
              Os conceitos principais, que viabilizaram o desenvolvimento do Node, so o de&nbsp;
              <a href="https://tableless.com.br/fluxo-de-execucao-assincrono-em-javascript-callbacks/">
                fluxo assncrono
              </a> e <a href="https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/EventLoop">loop de eventos</a>!
            </strong>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="nodejs"
              src="https://upload.wikimedia.org/wikipedia/commons/7/7e/Node.js_logo_2015.svg"
              style={{
                borderRadius: 0,
                height: 50,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="5.2. NPM">
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0 }}>
            <strong>Node Package Manager (ou simplesmente <a href="https://www.npmjs.com/">NPM</a>)</strong>, como o
            prprio nome sugere,  o gerenciador de pacotes Javascript para o <strong>Node</strong>.  responsvel
            por armazenar <strong>metadados</strong> sobre projetos, publicar em <strong>repositrios online</strong>
            &nbsp;e gerenciar <strong>dependncias</strong>.
            <br /><br />
             muito utilizado por <strong>bibliotecas e frameworks</strong> por ser uma plataforma simples de se manter
            e possuir um <strong>portal de buscas</strong>. Permite, tambm, a definio de <strong>scripts</strong>
            &nbsp;pr-definidos, facilitando muito o processo de <strong>desenvolvimento e build</strong> de projetos
            mais elaborados.
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="npm"
              src="https://upload.wikimedia.org/wikipedia/commons/d/db/Npm-logo.svg"
              style={{
                borderRadius: 0,
                height: 75,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingTop: 0 }}>
            <H3>Yarn</H3>
            Considerados pela comunidade como uma <a href="https://tableless.com.br/yarn-evolucao-do-npm/">evoluo</a>
            &nbsp;do <strong>NPM</strong>, o <strong>Yarn</strong>  um projeto&nbsp;
            <a href="https://canaltech.com.br/produtos/O-que-e-open-source/">open source</a> desenvolvido pelo&nbsp;
            <strong>Facebook</strong> e mantido por grandes empresas (a principal sendo o <strong>Google</strong>).
            <br /><br />
            Seu principal diferencial  o uso de <strong>cache</strong> para manter repositrios localmente, permitindo
            a <strong>instalao e manuteno offline</strong>. A sintaxe dos comandos  <strong>mais simples</strong>
            &nbsp;que a do NPM e seu <strong>desempenho  muito bom</strong> ao manter projetos de grande escala, se
            tornando uma preferncia em muitas empresas.
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="yarn"
              src="https://yarnpkg.com/assets/og_image.png"
              style={{
                borderRadius: 0,
                height: 100,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="5.3. Babel">
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0 }}>
            <strong>Babel</strong>, ou tambm chamado de <strong>Babel.js</strong>,  um&nbsp;
            <a href="https://pt.wikipedia.org/wiki/Compilador">compilador</a> e&nbsp;
            <a href="https://pt.stackoverflow.com/questions/189894/o-que-%C3%A9-transpila%C3%A7%C3%A3o">transpilador</a>
            &nbsp; para Javascript.  <strong>extremamente popular</strong> devido ao seu suporte contnuo s verses
            do ECMAScript e diversos <a href="https://github.com/babel/awesome-babel">plugins</a>.
            <br /><br />
            Sua principal funcionalidade  converter cdigo <strong>dos padres atuais para padres antigos</strong> e
            com maior suporte. Tambm interpreta a <strong>sintaxe JSX</strong> (muito utilizada em&nbsp;
            <a href="https://reactjs.org/">React</a>) e <strong>tipagem de dados</strong> (utilizada por&nbsp;
            <a href="https://flow.org/">Flow</a> e <a href="https://www.typescriptlang.org/">Typescript</a>).
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="babel"
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/02/Babel_Logo.svg/1200px-Babel_Logo.svg.png"
              style={{
                borderRadius: 0,
                height: 100,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingTop: 0 }}>
            <H3>preset-env</H3>
            Este <a href="https://babeljs.io/docs/en/next/presets">preset</a> do Babel  responsvel por escolher e
            utilizar a <strong>ltima verso do ECMAScript.</strong> Mantido para verses <strong>ES2015+</strong>, 
            uma configurao muito utilizada por sempre estar a par com a especificao mais recente.
            <br /><br />
            <strong>
                Nos casos onde  necessrio ter mais controle sobre a gerao de cdigo ou especificar detalhes sobre
                a plataforma-alvo,  recomendado utilizar mais configuraes alm do padro!
            </strong>
            <H3>babel-cli</H3>
            Alm das configuraes, plugins e presets, o Babel disponibiliza uma&nbsp;
            <strong>interface de linha de comando</strong> (ou apenas&nbsp;
            <a href="https://en.wikipedia.org/wiki/Command-line_interface">CLI</a>). Atravs dela  possvel&nbsp;
            <strong>compilar e especificar modificaes e regras especficas</strong>, se tornando
            uma ferramenta muito til para testes e gerao de cdigo.
            <br /><br />
            <strong>
              A <a href="https://babeljs.io/docs/en/babel-cli">documentao oficial</a> recomenda instalar o
              &quot;babel-cli&quot; localmente nos repositrios, por questes de compatibilidade e manuteno
              de projeto!
            </strong>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="es6"
              src="http://www.bobbyberberyan.com/wp-content/uploads/2016/12/ES6LOGO.svg"
              style={{
                borderRadius: 0,
                height: 100,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="5.4. ESLint">
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0 }}>
            <strong>ESLint</strong>  um <a href="https://en.wikipedia.org/wiki/Lint_(software)">linter</a> feito
            especificamente para Javascript.  <strong>muito customizvel</strong> e assim como o <strong>Babel</strong>
            &nbsp;possui uma srie de plugins e presets. De fato, pode ser utilizado&nbsp;
            <strong>em conjunto com o Babel</strong> como uma forma de pr-compilao. Alm de analisar cdigos na
            extenso <strong>.js</strong>, pode ser extendido para trabalhar
            com <strong>frameworks, sintaxes hbridas (como  o caso do JSX) e arquivos de teste</strong>.
            <br /><br />
            Os principais motivos para se utilizar um linter so:
            <ul>
              <li>
                <strong>Padronizao:</strong> garante que todos os desenvolvedores produzam um cdigo semelhante
              </li>
              <li>
                <strong>Legibilidade:</strong> define estruturas que fazem mais sentido e conseguem ser compreendidas
                por todos
              </li>
              <li>
                <strong>Manutenibilidade:</strong> pelos motivos anteriores, resulta numa identificao mais rpida
                e incisiva de erros
              </li>
            </ul>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="eslint"
              src="https://cdn.freebiesupply.com/logos/large/2x/eslint-logo-png-transparent.png"
              style={{
                borderRadius: 0,
                height: 100,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingTop: 0 }}>
            <H3>Padro Airbnb</H3>
            A equipe de desenvolvimento da <a href="https://www.airbnb.com.br/">Airbnb</a> especifica uma srie de&nbsp;
            <strong>padres de cdigo</strong> para diversas linguagens.&nbsp;
            <strong>Com o Javascript no  diferente:</strong> o &quot;guia de estilo&quot;
            (<a href="https://github.com/airbnb/javascript">styleguide</a>) define&nbsp;
            <strong>regras sintticas e semnticas</strong> para a escrita de cdigos.
            <br /><br />
            Como  um padro <strong>robusto e restritivo</strong>, detecta com mais facilidade estruturas&nbsp;
            <strong>legado, antipadro ou inadequadas</strong>. A mesma equipe que o especificou desenvolveu o
            <a href="https://airbnb.io/enzyme/docs/guides/jest.html">enzyme</a>, ferramenta muito utilizada na
            especificao de testes unitrios.
            <br /><br />
            <strong>O plugin da Airbnb com todas as configuraes pode ser utilizado no ESLint!</strong>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="airbnb"
              src="https://eslint.org/img/logos/airbnb.png"
              style={{
                borderRadius: 0,
                height: 50,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="5.5. Webpack">
        <CodeArea>
          <CodeComment style={{ paddingBottom: 0 }}>
            <strong>Webpack</strong>  um <strong>module bundler</strong> (&quot;empacotador&quot; de mdulos). Seu
            princpio bsico  <strong>juntar</strong> diversos arquivos <strong>.js ou de outras extenses</strong>
            &nbsp;de um projeto em um <strong>conjunto (bundle)</strong> de arquivos finais.
            <br /><br />
            Atravs de um <strong>grafo de dependncias</strong>, o&nbsp;
            <a href="https://medium.com/@gimenete/how-javascript-bundlers-work-1fc0d0caf2da">bundler</a> obtm
            informaes sobre <strong>como os arquivos so organizados e se comunicam</strong>. Assim,  determinada a
            melhor forma de <strong>empacotar cdigos</strong> de acordo com as configuraes passadas.
            <br /><br />
            <strong>
              Os pontos fortes do Webpack so a&nbsp;
              <a href="https://en.wikipedia.org/wiki/Minification_(programming)">minificao</a> e otimizao,
              responsveis por melhorar o desempenho e o tempo de carregamento de recursos.
            </strong>
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="webpack"
              src="https://cdn-images-1.medium.com/max/1600/1*gdoQ1_5OID90wf1eLTFvWw.png"
              style={{
                borderRadius: 0,
                height: 100,
              }}
            />
          </CodeComment>
        </CodeArea>
        <CodeArea>
          <CodeComment style={{ paddingTop: 0 }}>
            <H3>Princpios</H3>
            O Webpack segue alguns <strong>princpios bsicos de funcionamento.</strong> Estes precisam ser
            especificados para que o bundle seja gerado corretamente:
            <ul>
              <li>
                <strong>Entry:</strong> define <strong>um ou vrios</strong> arquivos de entrada, os quais sero usados
                para gerar o grafo de dependncias de acordo com <strong>imports/exports</strong>
              </li>
              <li>
                <strong>Output:</strong> especifica <strong>um ou vrios</strong> arquivos de sada, de acordo com os
                <strong>pontos de entrada</strong>
              </li>
              <li>
                <strong>Loaders:</strong> permitem carregar e empacotar arquivos de&nbsp;
                <strong>extenses diferentes</strong>. Por padro o Webpack interpreta arquivos <strong>.js</strong>,
                ento  necessrio utilizar <strong>loaders</strong> sempre que h referncias para outros formatos
              </li>
              <li>
                <strong>Plugins:</strong> so mdulos responsveis por gerar <strong>builds customizadas</strong> de
                cdigo. Possuem diversas funcionalidades e so aplicadas <strong>durante o processo de bundle</strong>
              </li>
            </ul>
            <H3>Dev Server</H3>
            <strong>Servidor de desenvolvimento em localhost,</strong> configurvel com execuo e&nbsp;
            recompilao em tempo real <strong>(chamado de &quot;watch mode&quot;).</strong> Permite executar
            requisies a servidores sem problemas e gera <strong>builds de desenvolvimento</strong> que podem ser
            utilizadas para testar o cdigo final.
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="webpack-schema"
              src="https://cdn-images-1.medium.com/max/800/0*ieedOgpOlC7UQBZM.png"
              style={{
                height: 350,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <Accordion title="5.6. Visual Studio Code">
        <CodeArea>
          <CodeComment>
            <strong>Extra do extra,</strong> pode ser considerado favoritismo!
            <br /><br />
            <strong>Visual Studio Code</strong>, ou simplesmente <strong>VSCode</strong>,  uma&nbsp;
            <strong>IDE</strong> comumente utilizada como editor de texto, desenvolvida pela&nbsp;
            <a href="https://code.visualstudio.com/">Microsoft</a> e lanado como&nbsp;
            <a href="https://pt.wikipedia.org/wiki/C%C3%B3digo_aberto">cdigo aberto</a>. Possui uma interface&nbsp;
            <strong>extremamente customizvel</strong> e uma <strong>comunidade ativa</strong> responsvel por manter
            o programa atualizado e <strong>desenvolver extenses.</strong>
            <br /><br />
            Possui <strong>suporte nativo</strong>  sintaxe&nbsp;
            <a href="https://code.visualstudio.com/docs/languages/javascript">Javascript</a> e variaes (Typescript,
            JSX, principalmente), com <strong>linter e snippets para autocomplete.</strong> Indo alm do Javascript,
            possui integrao com repositrios&nbsp;
            <a href="https://code.visualstudio.com/docs/editor/versioncontrol#_git-support">Git</a> e plugins para&nbsp;
            <a href="https://code.visualstudio.com/docs/azure/docker">Docker</a>.
            <br /><br />
            Recomendo ele como editor por ter uma <strong>interface mais intuitiva e fcil de customizar</strong>,
            alm de ser <strong>multiplataforma</strong> e ser <strong>leve</strong>. Para aqueles que vem
            do <a href="https://atom.io/">Atom</a> existe at um&nbsp;
            <a href="https://github.com/Microsoft/vscode-atom-keybindings">plugin</a> que utiliza os mesmos
            atalhos <strong>(e facilitam a vida)</strong>!
          </CodeComment>
          <CodeComment style={{ display: 'flex', alignItems: 'center' }}>
            <CentralizedImg
              alt="vscode"
              src="https://cdn.freebiesupply.com/logos/large/2x/visual-studio-code-logo-png-transparent.png"
              style={{
                borderRadius: 0,
                height: 75,
              }}
            />
          </CodeComment>
        </CodeArea>
      </Accordion>
      <NavLinks>
        <PrevLink to="/4-advanced">4. Avanado</PrevLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default Tools;



