import React, { Fragment } from 'react';
import {
  H1,
  H3,
  Content,
  InfoText,
  CodeArea,
  CodeComment,
  CodeFrame,
  CentralizedImg,
} from 'components/styles';
import Accordion from 'components/Accordion';
import { NavLinks, NextLink, PrevLink } from 'components/Links';

const Advanced = () => (
  <Fragment>
    <H1>4. Avanado</H1>
    <Content>
      <InfoText>
        <strong>Bruxarias, mas no geral truques e estruturas otimizadas.</strong> Continuando o processo de
        aprendizado, trago alguns&nbsp;
        <strong>
          <a href="https://addyosmani.com/resources/essentialjsdesignpatterns/book/">Design Patterns</a>, mtodos e
          tcnicas de&nbsp;
          <a href="https://medium.com/@cscalfani/so-you-want-to-be-a-functional-programmer-part-1-1f15e387e536">
            programao funcional
          </a>
        </strong>&nbsp;
        que muitas vezes ajudam a implementar cdigos mais legveis e eficientes. Muito do que  passado aqui depende
        das mudanas feitas no <strong>ES6</strong> e serve como leitura complementar deste captulo. Como lembrete, a
        compatibilidade dos mtodos passados aqui <strong>ainda  pequena</strong>, ento  extremamente recomendado
        usar um <strong>compilador</strong> se for us-los em algum cdigo de produo!
        <br /><br />
        Alguns tpicos citados aqui se tornaram <strong>padro ou so muito utilizados</strong> em verses ps-ES6, dei
        uma nfase maior neles! <strong>Novas funcionalidades</strong> foram implementadas sobre&nbsp;
        <strong>expresses regulares e iteradores assncronos</strong> no&nbsp;
        <strong>
          <a href="https://medium.com/front-end-hacking/javascript-whats-new-in-ecmascript-2018-es2018-17ede97f36d5">
            ES2018
          </a> (de fato j existe at o <a href="https://tc39.github.io/ecma262/">ES2019</a>!)
        </strong>, mas no cobri elas aqui devido a falta de suporte e uso.&nbsp;
        <strong>Para futuras leituras e aprofundamentos</strong> recomendo fortemente
        o manual da <a href="https://developer.mozilla.org/pt-BR/">MDN</a> (atualizado sempre com as novas convenes e
        tabelas de compatibilidade) e o <a href="http://frontendweekly.co/">Frontend Weekly</a> (muitas tcnicas e
        solues interessantes para problemas).
        <br /><br />
        <strong>Vamos nessa!</strong>
        <CentralizedImg
          alt="wizardry"
          src="https://media.giphy.com/media/ms4x9Ipgego8g/giphy.gif"
          style={{
            marginTop: '1em',
            height: 200,
          }}
        />
      </InfoText>
      <Accordion title="4.1. IIFE">
        <CodeArea>
          <CodeComment>
            <strong>Immediately Invoked Function Expression</strong>, ou simplesmente <strong>IIFE</strong>,  um
            padro de cdigo que possui duas funcionalidades:
            <ul>
              <li>
                Declarar uma funo <strong>nomeada ou annima</strong> no contexto local
              </li>
              <li>
                <strong>Execut-la logo em seguida</strong>, removendo qualquer referncia aps sua execuo
              </li>
            </ul>
            Estruturas desse tipo so muito teis quando se tem a necessidade de realizar operaes num&nbsp;
            <strong>escopo separado e temporrio</strong>.
            <br /><br />
            <strong>IIFEs podem retornar valores normalmente!</strong>
          </CodeComment>
          <CodeFrame
            title="advanced/1_iife.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/s5cq3xv9/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.2. Closures">
        <CodeArea>
          <CodeComment>
            <strong>Closures (fechamento)</strong> so um mecanismo da linguagem utilizado para &quot;privatizar&quot;
            atributos dentro de escopos locais. Devem ser utilizados quando se deseja que&nbsp;
            <strong>variveis, funes e objetos</strong> no sejam acessados fora de uma funo ou objeto.
            <br /><br />
            Observe nos exemplos que os atributos modificados so&nbsp;
            <strong>atualizados normalmente dentro do escopo</strong>, mas para obt-los  necessrio que&nbsp;
            <strong>mtodos &quot;pblicos&quot; retornem seus valores</strong> explicitamente.
            <br /><br />
            <strong>Esse mecanismo deriva do conceito de encapsulamento na Orientao a Objetos!</strong>
          </CodeComment>
          <CodeFrame
            title="advanced/2_closures.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/kpny0hdx/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.3. Currying">
        <CodeArea>
          <CodeComment>
            Nomeada pelo seu criador, o matemtico&nbsp;
            <a href="https://en.wikipedia.org/wiki/Haskell_Curry">Haskell Curry</a>, essa tcnica combinatria
            envolve <strong>reduzir o nmero de argumentos (ordem)</strong> de uma funo de acordo com seu uso e
            valores que so comumente passados. , tambm, uma forma de&nbsp;
            <a href="https://pt.wikipedia.org/wiki/C%C3%A1lculo_lambda">clculo lambda</a>, aonde as operaes so
            feitas <strong>implicitamente ou de forma encadeada</strong>.
            <br /><br />
            Uma viso matemtica pode simplificar como esse processo funciona:
            <ul>
              <li>
                Uma funo <strong>f(x, y)</strong> ser executada muitas vezes para um mesmo valor de&nbsp;
                <strong>x</strong>
              </li>
              <li>
                Faz sentido, ento, gerar uma outra funo <strong>g(y)</strong> que j recebe o valor de x
                implicitamente
              </li>
              <li>
                Ento, podemos quebrar a funo <strong>f(x, y)</strong> em <strong>f&apos;(x)(y)</strong> e
                declarar <strong>g(y) = f(valor)(y)</strong>
              </li>
              <li>
                <strong>Isso  currying!</strong>
              </li>
            </ul>
            <strong>
              Uma vez que uma funo passou pelo processo de currying,  possvel revert-la ao seu &quot;estado&quot;
              original, tornando essa uma estrutura muito flexvel e utilizada em Javascript e outras linguagens!
            </strong>
          </CodeComment>
          <CodeFrame
            title="advanced/3_currying.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/aLnztq6u/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.4. forEach(), map(), filter() e reduce()">
        <CodeArea>
          <CodeComment>
            Aproveitando ao mximo a&nbsp;
            <a href="https://medium.com/opensanca/imperativo-ou-declarativo-3e6dffbf301c">programao declarativa</a>,
            foram definidos novos mecanismos para trabalhar com <strong>Arrays e objetos</strong>. As funes&nbsp;
            <strong>forEach(), map(), filter() e reduce()</strong> vieram para&nbsp;
            <strong>simplificar e otimizar</strong> operaes frequentes.
            <br /><br />
            Em todas, o <a href="https://pt.wikipedia.org/wiki/Modus_operandi">modus operandi</a>  o mesmo: um&nbsp;
            <strong>iterador declarativo</strong> assume os valores de elementos em uma lista.
            <ul>
              <li>
                <strong>forEach():</strong> intuitivamente itera sobre <strong>todos os elementos</strong>, um a um.
                Seu funcionamento  <strong>idntico</strong> ao lao <strong>for-of</strong>;
              </li>
              <li>
                <strong>map():</strong> permite aplicar transformaes em <strong>todos os elementos</strong>. Pode ser
                utilizado para <strong>inserir, remover ou selecionar atributos</strong> em alguns casos;
              </li>
              <li>
                <strong>filter():</strong> recupera elementos de acordo com o resultado de uma funo.  a forma
                mais <strong>simples e no-recursiva</strong> de gerar novos Arrays condicionalmente!
              </li>
              <li>
                <strong>reduce():</strong> itera sobre <strong>todos os elementos</strong> e mantm um resultado
                atravs de um acumulador. Pode receber um <strong>valor inicial</strong> e percorrer&nbsp;
                <strong>objetos.</strong>
              </li>
            </ul>
            <strong>
              forEach(), apesar de sua simplicidade,  considerado antipadro por alguns frameworks.
              Mas no tem problema: map() tem a mesma funcionalidade se os elementos no forem modificados!
            </strong>
          </CodeComment>
          <CodeFrame
            title="advanced/4_iterators.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/mu9rjhdx/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.5. Funes assncronas">
        <CodeArea>
          <CodeComment>
            Aplicaes <strong>&quot;do mundo real&quot;</strong> quase sempre ocorrem de maneira&nbsp;
            <strong>assncrona</strong>, onde o tempo de chamada ou resposta&nbsp;
            <strong>no  instantneo ou previsvel</strong>. Seja uma requisio a um servidor ou at uma interao do
            usurio numa interface,  necessario que a execuo seja <strong>no-bloqueante</strong> e retorne aes
            de <strong>sucesso ou erro</strong> atravs de&nbsp;
            <a href="https://tableless.com.br/fluxo-de-execucao-assincrono-em-javascript-callbacks/">callbacks</a>.
            <br /><br />
            As funes <strong>setTimeout e setInterval</strong> so assncronas do tipo <strong>timer</strong>, e
            executam callbacks <strong>uma vez aps um timeout ou repetidamente em um intervalo definido</strong>,
            respectivamente. <strong>Elas so cancelveis!</strong>
            <H3>Callback Hell</H3>
            <strong>Aninhar vrias funes</strong>  um erro muito comum quando se programa com fluxos assncronos!
            De fato,  intuitivo que o tratamento das respostas seja feito com&nbsp;
            <strong>blocos e lgicas diferentes</strong>, mas se isso  feito progressivamente atravs de callbacks
            temos o fenmeno do <a href="http://callbackhell.com/">Callback Hell</a>.
            <br /><br />
            <strong>Os principais sintomas so ilegibilidade de cdigo e dificuldade na manuteno!</strong>
          </CodeComment>
          <CodeFrame
            title="advanced/5_asyncs.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/zfyaw2qk/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.6. Promises">
        <CodeArea>
          <CodeComment>
            Promises permitem <strong>declarar e operar</strong> funes assncronas de maneira muito mais&nbsp;
            <strong>simples e organizada.</strong> O fluxo  <strong>dividido em trs estados:</strong>
            <ul>
              <li>
                <strong>Pendente:</strong> a Promise foi declarada e est <strong>pronta para ser executada.</strong>
                &nbsp;Nesse ponto, a funo foi chamada normalmente e <strong>ainda no temos o seu resultado!</strong>
              </li>
              <li>
                <strong>Resolvida:</strong> a operao foi completada com <strong>sucesso</strong> e poder continuar
                seu fluxo normalmente. Um callback, definido em <strong>then()</strong>, &nbsp;
                <strong>&quot;ento&quot;</strong> executado;
              </li>
              <li>
                <strong>Rejeitada:</strong> houve algum <strong>erro</strong> nos parmetros passados ou na execuo.
                Um callback, definido em <strong>catch()</strong>,  <strong>&quot;pego&quot;</strong> e dever lidar
                com a falha;
              </li>
            </ul>
            <H3>async/await</H3>
            <strong>Talvez a nomenclatura mais intuitiva do Javascript!</strong>&nbsp;
            <br /><br />
            <a href="https://en.wikipedia.org/wiki/Syntactic_sugar">Syntax sugar</a> para Promises, os operadores&nbsp;
            <strong>async/await</strong> declaram que uma determinada funo  <strong>assncrona</strong> e que
            ir <strong>esperar</strong> a execuo de outras funes assncronas.
            Seu funcionamento  idntico ao de uma Promise, com fluxos <strong>then() e catch()</strong> para&nbsp;
            <strong>sucesso ou falha</strong>, respectivamente.
            <br /><br />
            <strong>
              Fundamentalmente permite aninhar Promises sem problemas de execuo ou rudos de sintaxe, lidando
              diretamente com o Callback Hell!
            </strong>
          </CodeComment>
          <CodeFrame
            title="advanced/6_promises.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/4wzbeqd8/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="4.7. Generators">
        <CodeArea>
          <CodeComment>
            <strong>Hbrido entre funes assncronas e sncronas</strong>, as&nbsp;
            <strong>Generators (function *)</strong> so funes controladas atravs de um <strong>iterador</strong>.
            Em termos no-tcnicos, so blocos de cdigo que possuem a&nbsp;
            <strong>execuo &quot;em blocos&quot;</strong> e  possvel determinar quando o prximo bloco ser
            executado (e o que ele far).
            <br /><br />
            Podem <strong>receber e retornar valores</strong> em qualquer ponto da execuo. O ponteiro da iterao
            deve ser armazenado numa <strong>varivel</strong>, o fluxo prossegue&nbsp;
            <strong>linearmente e  irreversvel.</strong>
            <br /><br />
            <strong>No pode ser usado com arrow functions!</strong>
            <H3>yield ou return?</H3>
            Embora ambos operadores sejam responsveis por <strong>retornar valores</strong>, eles so&nbsp;
            <strong>semanticamente</strong> diferentes:
            <ul>
              <li>
                <strong>yield:</strong> retorna valores numa determinada iterao <strong>next()</strong> de uma
                generator. Ao fazer isso, todo o cdigo que vem <strong>depois</strong> de um yield  executado numa
                prxima iterao, <strong>continuando o fluxo</strong>;
              </li>
              <li>
                <strong>return:</strong> retorna um valor <strong>final</strong> e no permite executar mais nada
                aps sua execuo. Pode ser usado <strong>junto com yields</strong> numa generator para finalizar suas
                iteraes!
              </li>
            </ul>
            <strong>
               possvel passar novos valores para cada iterao next() atravs de valores em yield. Essa prtica  um
              tanto confusa e requer uma&nbsp;
              <a href="https://codeburst.io/what-are-javascript-generators-and-how-to-use-them-c6f2713fd12e">
                compreenso melhor
              </a> de como as Generators funcionam!
            </strong>
          </CodeComment>
          <CodeFrame
            title="advanced/7_generators.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/g7qp4omx/1/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <NavLinks>
        <PrevLink to="/3-es6">3. ES6</PrevLink>
        <NextLink to="/5-tools">5. Ferramentas</NextLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default Advanced;



