import React, { Fragment } from 'react';
import {
  H1,
  H3,
  Content,
  InfoText,
  CodeArea,
  CodeComment,
  CodeFrame,
  CentralizedImg,
} from 'components/styles';
import Accordion from 'components/Accordion';
import { NavLinks, NextLink, PrevLink } from 'components/Links';

const ES6 = () => (
  <Fragment>
    <H1>3. ES6</H1>
    <Content>
      <InfoText>
        <strong>Novo. Moderno. Simplificado.</strong> O&nbsp;
        <a href="http://www.ecma-international.org/ecma-262/6.0/">ECMAScript6</a>, ou
        simplesmente <strong>ES6</strong>, foi uma especificao lanada em junho de 2015 pela&nbsp;
        <a href="https://www.ecma-international.org/">ECMA (European Computer Manufacturers Association)</a> responsvel
        por <strong>padronizar e flexibilizar</strong> estruturas antigas e trazer&nbsp;
        <strong>novos conceitos</strong>  linguagem. Conforme visto nos captulos anteriores, muito do que tornava JS
        uma linguagem confusa e limitada foi reduzido ou corrigido nessa verso. Neste captulo explico as
        principais <strong>mudanas e novidades</strong> junto com exemplos de cdigo e alguns truques.
        <br /><br />
        <strong>
           importante lembrar que ainda no h suporte em alguns navegadores! (tabela de compatibilidade pode ser
          consultada <a href="http://kangax.github.io/compat-table/es6/">aqui</a>)
        </strong>&nbsp;
        Mas no tem problema! Existem <strong>compiladores</strong> que conseguem converter o cdigo &quot;novo&quot;
        para os padres antigos e suportados universalmente. O mais conhecido e utilizado  o&nbsp;
        <a href="https://babeljs.io/">Babel</a>, devido a sua praticidade e o suporte para plugins e configuraes
        externas.&nbsp;
        <a href="https://blog.tecsinapse.com.br/utilizando-es6-no-node-js-com-babel-js-430346d68794">Aqui</a>&nbsp;
        tem um tutorial que ensina como configurar o Babel com o <strong>preset-env</strong> (sempre atualizado com
        o ECMA!).
        <br /><br />
        <strong>Vamos nessa!</strong>
        <br />
        <CentralizedImg
          alt="programming"
          src="https://imasters.com/wp-content/uploads/sites/24/2016/09/4_Progresso4.gif"
          style={{
            marginTop: '1em',
            height: 200,
          }}
        />
      </InfoText>
      <Accordion title="3.1. Declarao e escopo de variveis">
        <CodeArea>
          <CodeComment>
            Novos tipos de variveis foram introduzidos no ES6: <strong>const e let</strong>.
            Elas operam de maneira similar s <strong>vars</strong> em declarao e associao de valores, com a
            diferena fundamental de que elas obedecem ao <strong>escopo de bloco</strong>.&nbsp;
            <br /><br />
            <strong>
              &quot;Consts&quot; recebem apenas um valor inicial e no podem ser reatribudas como as &quot;lets&quot;.
              No entanto, se um objeto for &quot;const&quot; ele pode ter suas propriedades modificadas normalmente!
            </strong>
            <H3>Escopo de bloco</H3>
            Variveis do tipo <strong>const e let</strong> obedecem, alm dos escopos &quot;normais&quot; de funo,
            escopos definidos por blocos. Isso significa que variveis desses tipos definidas em&nbsp;
            <strong>condies, laos e blocos explcitos (definidos entre {'{ }'})</strong>
            &nbsp;so encapsuladas e s podem ser acessadas <strong>dentro</strong> dessas estruturas.
            Por conta desse detalhe, elas <strong>evitam o instanciamento indevido causado pelo hoisting!</strong>
            <br /><br />
            <strong>Note que os blocos no definem contexto, apenas reservam um espao para variveis locais!</strong>
          </CodeComment>
          <CodeFrame
            title="es6/1_variables.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/dnbxcur8/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="3.2. Parmetros default e rest">
        <CodeArea>
          <CodeComment>
            Novidades para a declarao de funes foram especificadas, trazendo a possibilidade de definir&nbsp;
            <strong>valores-padro e parmetros de &quot;lastro&quot; (rest)</strong>.
            <ul>
              <li>
                <strong>Valores-padro</strong> so utilizados para atribuir valores  parmetros quando eles no so
                definidos ao chamar uma funo
              </li>
              <li>
                <strong>Parmetros rest</strong> definem valores passados indefinidamente, so tratados como objetos
                ou Arrays pela funo
              </li>
            </ul>
            Os parmetros <strong>rest</strong> so bem teis quando h <strong>parmetros opcionais</strong> ou se
            deseja passar <strong>um nmero indeterminado ou grande de parmetros</strong>.
            <br /><br />
            <strong>
              Eles so diferentes dos parmetros&nbsp;
              <a href="https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/Reference/Functions/rest_parameters">
                arguments
              </a>!
            </strong>
          </CodeComment>
          <CodeFrame
            title="es6/2_params.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/987u2oak/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="3.3. Arrow functions">
        <CodeArea>
          <CodeComment>
            Aqui est um exemplo de simplificao de sintaxe. As funes &quot;seta&quot;&nbsp;
            <strong>(arrow functions)</strong> so a evoluo das funes convencionais:&nbsp;
            <strong>nativamente annimas</strong> (o nome  definido pela varivel que a armazena) e com&nbsp;
            <strong>retorno direto</strong>, eliminando a necessidade da sintaxe <strong>return</strong> quando ela
             redundante.
            <br /><br />
            <strong>
              Lembra do&nbsp;
              <a href="https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/Reference/Global_Objects/Function/bind">
                bind
              </a>?
            </strong>&nbsp;
            Ele  feito automaticamente para o <strong>contexto onde a arrow foi declarada!</strong> Esse detalhe 
            muito til para a definio de <strong>classes e polimorfismo</strong>, que sero explicados adiante.
          </CodeComment>
          <CodeFrame
            title="es6/3_arrows.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/qzbcp3Lh/1/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="3.4. Destructuring">
        <CodeArea>
          <CodeComment>
            Mais um exemplo de simplificao de sintaxe: o <strong>destructuring recupera e mapeia</strong> atributos de
            um objeto ou ndices de um Array em <strong>variveis const ou let</strong>. Ao fazer isso,&nbsp;
            <strong>elimina-se a atribuio redundante</strong> de recuperar valores e associ-los um a um em variveis,
            reduzindo consideravelmente o tamanho do cdigo.
            <br /><br />
            Note que qualquer modificao no objeto/Array original <strong>no</strong> atualiza os valores das
            variveis. <strong>O inverso se aplica para modificaes em variveis!</strong>
            <br /><br />
            <strong>
              Essa sintaxe pode ser combinada com parmetros rest e simplificar muito seu cdigo!
            </strong>
          </CodeComment>
          <CodeFrame
            title="es6/4_destructuring.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/udhof8tb/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="3.5. Pseudo Orientao a Objetos">
        <CodeArea>
          <CodeComment>
            O paradigma de&nbsp;
            <a
              href="https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/Introduction_to_Object-Oriented_JavaScript"
            >
              Programao Orientada a Objetos
            </a>&nbsp;
            foi especificado para a linguagem como forma
            de trazer uma sintaxe <strong>simples e funcional</strong> para a abstrao de&nbsp;
            <strong>entidades em objetos.</strong> Com ele,  possvel definir relaes de&nbsp;
            <strong>herana, encapsulamento, polimorfismo e modularidade</strong> entre objetos de maneira mais
            intuitiva e funcional do que como era feito em verses anteriores.
            <H3>Por que &quot;pseudo&quot;?</H3>
            Ao contrrio do que acontece em boa parte das linguagens orientadas a objeto&nbsp;
            <strong>(como exemplo Java, Python e C#)</strong>, quando criamos um objeto de uma classe em Javascript
             feita uma <strong>referncia ao objeto &quot;original&quot; da classe</strong>. Isso significa que
            quando  feita uma atribuio do tipo <strong>new Classe()</strong> no criamos uma instncia nova, e sim
            uma referncia!
            <br /><br />
            <strong>O mesmo comportamento acontece quando aplicamos polimorfismo!</strong> As classes herdadas fazem
            referncia  classe &quot;pai&quot;, e o <strong>Go-Fish (lembra dele?)</strong>  responsvel por buscar
            os valores e mtodos entre os objetos das classes.
            <H3>Getters/Setters</H3>
            Alm das operaes usuais entre classes, o ES6 trouxe uma sintaxe que permite encapsular dados de forma
            &quot;segura&quot;. Ao definir valores internos com <strong>this</strong>, funes do tipo&nbsp;
            <strong>get ou set</strong> so responsveis por <strong>recuperar ou atualizar</strong> atributos.
          </CodeComment>
          <CodeFrame
            title="es6/5_oop.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/ydxwq8bj/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="3.6. Mdulos">
        <CodeArea>
          <CodeComment>
            <strong>&quot;Mdulos&quot;</strong>  o nome dado para&nbsp;
            <strong>objetos, funes, variveis e classes</strong> que so exportados e importados explicitamente
            entre arquivos JS. Antes da sintaxe <strong>export/import</strong>, isso era comumente feito utilizando o
            operador <strong>require()</strong> da API&nbsp;
            <a href="https://nodejs.org/docs/latest/api/modules.html">CommonJS</a> ou outros frameworks que
            implementavam essa ao.
            <br /><br />
            <strong>
              No JSFiddle no  possvel definir mltiplos arquivos, mas indico no cdigo como deve ser a organizao
              dos arquivos!
            </strong>
          </CodeComment>
          <CodeFrame
            title="es6/6_modules.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/n9Lbuc07/8/embedded/js/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <NavLinks>
        <PrevLink to="/2-basics">2. Bsico</PrevLink>
        <NextLink to="/4-advanced">4. Avanado</NextLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default ES6;



