import React, { Fragment } from 'react';
import {
  H1,
  H3,
  Content,
  InfoText,
  CodeArea,
  CodeComment,
  CodeFrame,
  CentralizedImg,
} from 'components/styles';
import Accordion from 'components/Accordion';
import { NavLinks, NextLink, PrevLink } from 'components/Links';

const Basics = () => (
  <Fragment>
    <H1>2. Bsico</H1>
    <Content>
      <InfoText>
        <strong>Os cdigos comeam por aqui!</strong> Nesse captulo, mostro conceitos bsicos e fundamentos do
        JavaScript. Note que os exemplos esto no padro &quot;antigo&quot; <strong>(pr-ES6)</strong>, as diferenas
        sero explicadas depois. Para aqueles que no esto familiarizados, a linguagem passou por&nbsp;
        <strong>evolues tcnicas e sintticas</strong> que flexibilizaram e limitaram problemas que eram causados por
        diferenas semnticas entre JS e outras linguagens de programao.
        <br /><br />
        Aqui, so exemplificadas&nbsp;
        <strong>operaes, atribuies, tipos de dados, estruturas e caractersticas de interpretao</strong>&nbsp;
        que so essenciais para entender o funcionamento e o comportamento dos cdigos. Aproveitei para enfatizar,
        tambm, alguns pontos que so considerados <strong>legado ou antipadro</strong>, e como eles so contornados
        pelo ES6 em diante.&nbsp;
        <strong>
           importante que se tenha um conhecimento mnimo de lgica de programao para acompanhar as explicaes!
        </strong>&nbsp;
        Em pontos onde h termos no to comuns eu deixei links complementares para ajudar os estudos :)
        <br /><br />
        <strong>Vamos nessa!</strong>
        <CentralizedImg
          alt="programming"
          src="https://media.giphy.com/media/wpBCnSSBCbh3q/giphy.gif"
          style={{
            marginTop: '1em',
            height: 200,
          }}
        />
      </InfoText>
      <Accordion title="2.1. Hello World">
        <CodeArea>
          <CodeComment>
            Reza a lenda que se voc aprender uma linguagem sem fazer um <strong>&quot;Hello World!&quot;</strong> uma
            maldio  passada para sua vida. Felizmente,  to simples quanto chamar o mtodo&nbsp;
            <strong>console.log();</strong>
          </CodeComment>
          <CodeFrame
            title="basics/1_hello.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/b1an8Lf7/2/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.2. Operadores">
        <CodeArea>
          <CodeComment>
            As operaes <strong>algbricas e booleanas</strong> funcionam como esperado (e com sintaxe muito familiar).
            No exemplo, as operaes so aplicadas em valores <strong>number e boolean</strong>, mas ser mostrado
            adiante que  possvel fazer entre diversos tipos de dados.
            <H3>== vs ===</H3>
            Observe que nas comparao ao lado h o uso de <strong>===</strong>. Este operador pode parecer um pouco
            estranho, mas sua utilidade  muito grande. Alm de comparar os valores, como o operador&nbsp;
            <strong>==</strong> tradicionalmente faz, o <strong>=== (tambm chamado de eqeqeq)</strong>&nbsp;
            compara os tipos de dados dos operandos.
            <br /><br />
            <strong>
              De fato, a utilidade do ===  to grande que ele se tornou padro na linguagem e deve ser utilizado sempre
              que for necessrio comparar dois valores.
            </strong>
          </CodeComment>
          <CodeFrame
            title="basics/2_operators.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/gq0uwamt/1/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.3. Comentrios">
        <CodeArea>
          <CodeComment>
            Assim como nas demais linguagens,  possvel <strong>(e recomendado)</strong> colocar comentrios nos
            cdigos. Novamente, a sintaxe  muito familiar e funciona como esperado, onde os comentrios podem ser
            colocados <strong>aps as expresses em uma linha, em uma linha separada ou ocupar vrias linhas</strong>.
          </CodeComment>
          <CodeFrame
            title="basics/3_comments.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/fhse2br5/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.4. Tipos de dados">
        <CodeArea>
          <CodeComment>
            Existem duas &quot;classes&quot; de tipos de dados:
            <ul>
              <li>
                Os tipos <strong>primitivos</strong> so tratados como entidades <strong>imutveis e bsicas</strong>,
                nas quais os valores j esto definidos previamente e no podem ser modificados.
              </li>
              <li>
                O tipo <strong>Object</strong>  utilizado para definir outras &quot;macros&quot; na linguagem, tanto a
                nvel de implementao <strong>(estruturas presentes na linguagem)</strong> quanto a nvel de usurio
                <strong>(objetos, classes e entidades criadas em cdigo).</strong>
              </li>
            </ul>
            No exemplo, so verificados os tipos de alguns valores <strong>primitivos e Object</strong>, sendo eles:
            <ul>
              <li>
                <strong>String:</strong> valores textuais
              </li>
              <li>
                <strong>Number:</strong> valores numricos reais
              </li>
              <li>
                <strong>Boolean:</strong> true/false
              </li>
              <li>
                <strong>Object:</strong> estruturas de objeto, instncias de classe, macros, entre outros
              </li>
              <li>
                <strong>Function:</strong> funes
              </li>
              <li>
                <strong>Null:</strong> valor nulo (diferente de undefined)
              </li>
              <li>
                <strong>Undefined:</strong> entidade indefinida (sem valor)
              </li>
            </ul>
            Observe que <strong>Array</strong>  um valor do tipo <strong>Object</strong>. Esta  uma macro da
            linguagem!
            <H3>typeof e instanceof</H3>
            Estes operadores so utilizados para indicar <strong>tipo ou instncia de classe</strong> para valores e
            variveis. Use-os sempre que estiver na dvida, tambm so teis para verificar&nbsp;
            <strong>valores indefinidos, objetos vazios e outras condies &quot;tipadas&quot;.</strong>
            <H3>Concatenao entre tipos</H3>
            Aqui as coisas ficam um tanto confusas: alguns tipos podem ser concatenados para gerar novos valores, mas
            no  recomendado, porm, que isso seja feito intuitivamente pelo operador&nbsp;
            <strong>&quot;+&quot;. Para ilustrar ainda melhor: </strong>
            <br /><br />
            <iframe
              title="destroyallsoftware wat"
              width="100%"
              height="315"
              src="https://www.youtube.com/embed/BrB0oaSz8Nk"
              frameBorder="0"
            />
          </CodeComment>
          <CodeFrame
            title="basics/4_datatypes.js"
            width="100%"
            height="600"
            src="//jsfiddle.net/santiagogustavo/hyemn3fo/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.5. Variveis">
        <CodeArea>
          <CodeComment>
            Novamente a sintaxe  familiar: para instanciar uma varivel,&nbsp;
            <strong>declara-se ela com um nome e atribui-se um valor (no necessariamente na declarao).</strong>&nbsp;
            Todos os operadores definidos at agora podem ser utilizados e o resultado pode ser armazenado.
            <br /><br />
            <strong>
               importante mencionar que nestes exemplos foi propositalmente utilizada a sintaxe &quot;var&quot; para
              fins didticos.&nbsp;
            </strong>
            A partir do ES6 (que  explicado no prximo captulo), <strong>&quot;vars&quot;</strong> se tornaram
            obsoletas e foram substitudas por outras estruturas.
            <H3>Tipagem dinmica</H3>
            As variveis em JS so <strong>fracamente tipadas</strong>. Isso significa que, uma vez que uma varivel
             instanciada, ela <strong>pode receber valores de qualquer tipo de dado</strong> e o&nbsp;
            <strong>valor/tipo pode ser modificado em qualquer instante.</strong>
            <br /><br />
             uma boa prtica, porm,&nbsp;
            <strong>documentar e manter as variveis em seus tipos &quot;originais&quot;</strong> para evitar
            problemas de execuo. Frameworks, bibliotecas e extenses da linguagem, como exemplo&nbsp;
            <a href="https://www.typescriptlang.org/">Typescript</a> e&nbsp;
            <a href="https://www.npmjs.com/package/prop-types">PropTypes</a>, exigem que os tipos sejam definidos e
            fazem a comparao com os valores recebidos.
            <H3>Backtick</H3>
            O operador <strong>`` (tambm chamado de backtick)</strong>  muito utilizado para exibir valores de
            variveis dentro de uma <strong>String</strong>. , inclusive, recomendado utilizar backticks no lugar
            de <strong>&quot;+&quot;</strong> quando for necessrio concatenar&nbsp;
            <strong>Strings!</strong>
          </CodeComment>
          <CodeFrame
            title="basics/5_variables.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/xh84zLjo/1/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.6. Condicionais">
        <CodeArea>
          <CodeComment>
            Assim como nas demais linguagens, as estruturas condicionais so definidas e funcionam como esperado:
            <ul>
              <li>
                <strong>if/else, if/else if/else:</strong> avaliam o resultado de uma comparao ou expresso e
                executam determinados blocos de cdigo.
              </li>
              <li>
                <strong>switch/case:</strong> compara o valor de uma varivel e encaminha para casos especficos&nbsp;
                <strong>(ou para um caso &quot;default&quot;)</strong>
              </li>
            </ul>
            <H3>Comparador ternrio</H3>
            A comparao por&nbsp;
            <strong>
              [condio] ? [fluxo verdadeiro] : [fluxo falso] (tambm chamada de comparador ternrio)&nbsp;
            </strong>
             um <a href="https://en.wikipedia.org/wiki/Syntactic_sugar">syntax sugar</a> em JS.
            Ela substitui diretamente condies <strong>if/else</strong>, e  muito utilizada para fazer atribuies
            condicionais e renderizar blocos dinmicos em <a href="https://reactjs.org/">React</a>.
          </CodeComment>
          <CodeFrame
            title="basics/6_conditionals.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/4Lvy9w0g/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.7. Laos e Iteradores">
        <CodeArea>
          <CodeComment>
            Laos, tambm chamados de <strong>estruturas de repetio</strong>, so definidos para blocos de cdigo que
            devem ser executados repetidamente. A repetio ocorre com a declarao de um&nbsp;
            <strong>iterador (em um intervalo de valores) ou uma condio (enquanto ela for satisfeita)</strong>.
            <br /><br />
            Existem diversas formas de se escrever um lao, conforme pode ser visto nos exemplos atravs das
            estruturas <strong>for, while e do/while</strong>. Note que em alguns deles h o uso de um&nbsp;
            <strong>iterador</strong>, que  atualizado a cada execuo do bloco. No caso&nbsp;
            <strong>while(true)</strong> o cdigo  executado indeterminadamente (condio sempre  verdadeira) e sai
            da repetio atravs de uma condio em <strong>if</strong> e o operador <strong>break.</strong>
            <H3>Iteradores declarativos</H3>
            JS implementa, para valores do tipo <strong>Array</strong>,&nbsp;
            <a href="https://medium.com/opensanca/imperativo-ou-declarativo-3e6dffbf301c">funes declarativas</a> que
            permitem percorrer laos sem a necessidade de especificar um domnio de valores.  o caso dos laos&nbsp;
            <strong>for/in e for/of:</strong>
            <ul>
              <li>
                No h a necessidade de especificar valores inicial e final para iterar
              </li>
              <li>
                <strong>Em for/in,</strong> a varivel iteradora assume o <strong>ndice</strong> atual
                (posio no Array)
              </li>
              <li>
                <strong>Em for/of,</strong> a varivel iteradora assume o <strong>valor</strong> da posio atual
              </li>
            </ul>
            <H3>Programao funcional</H3>
            A partir do <strong>ES6</strong>, foram implementadas funes baseadas em&nbsp;
            <a href="https://tableless.com.br/entendendo-programacao-funcional-em-javascript/">
              programao funcional
            </a>.
            No captulo <a href="/4-advanced">4. Avanado</a>, h uma explicao detalhada sobre como eles funcionam e
            facilitam muitos fluxos recursivos.
            <br /><br />
            <strong>
              A estrutura &quot;for&quot; com iterador explcito (primeiro exemplo) caiu em desuso e  considerado
              antipadro em algumas configuraes do <a href="https://eslint.org/">ESLint</a>.
            </strong>
          </CodeComment>
          <CodeFrame
            title="basics/7_loops.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/co9wj8sf/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.8. Funes">
        <CodeArea>
          <CodeComment>
            Funes definem blocos de cdigo que so <strong>chamados e executados</strong> em qualquer ponto da
            aplicao. As funes podem <strong>receber parmetros</strong> e <strong>retornar valores</strong> de
            quaisquer tipos de dados. <strong> possvel at receber e retornar outras funes!</strong>
            <ul>
              <li>
                Os valores de retornos podem ser <strong>armazenados em variveis</strong> ou&nbsp;
                <strong>usados em expresses</strong>. As funes so <strong>executadas</strong> e retornam
                seus valores <strong>antes</strong> que qualquer operao ou expresso seja calculada, como pode ser
                visto ao usar <strong>console.log()</strong> no exemplo
              </li>
              <li>
                Passar <strong>objetos como parmetro</strong>  uma boa prtica para simplificar e flexibilizar os
                dados necessrios para executar uma funo
              </li>
              <li>
                Funes <strong>annimas</strong> podem ser definidas, armazenadas e executadas atravs de variveis,
                como uma forma de&nbsp;
                <a href="https://medium.com/@ahlechandre/lambda-calculus-com-javascript-5db88c3b45a">
                  expresso lambda
                </a>
              </li>
            </ul>
          </CodeComment>
          <CodeFrame
            title="basics/8_functions.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/4nfg9qcz/2/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.9. Escopo e hoisting">
        <CodeArea>
          <CodeComment>
            Este  um dos pontos mais interessantes e complexos da linguagem. Por definio, variveis possuem um&nbsp;
            <strong>escopo</strong>, algo que pode ser explicado como sua <strong>localizao relativa</strong> no
            cdigo. Existem dois tipos de escopo:
            <ul>
              <li>
                <strong>Global:</strong> &quot;raiz&quot; do cdigo, escopo mais externo e final
              </li>
              <li>
                <strong>Local:</strong> definido por funes <strong>(usando var)</strong> ou por blocos de cdigo&nbsp;
                <strong>(em ES6)</strong>, podem ser aninhados
              </li>
            </ul>
            <H3>Hoisting</H3>
            Traduzindo literalmente:
            <br /><br />
            <strong>&ensp;&ensp;&ensp;&ensp;to hoist {'{v.}'}:</strong> iar, alar
            <br /><br />
            Traduzindo em termos tcnicos,  uma caracterstica <strong>muito importante</strong> do Javascript: as
            variveis declaradas no cdigo so <strong>&quot;jogadas para cima&quot;</strong>, como se tivessem sido
            declaradas no topo de cada escopo.
            <br /><br />
            Esse processo  feito devido a um mecanismo de procura pelas variveis sempre que elas so referenciadas
            em expresses. Chamado de <strong>Go-Fish</strong>, ele funciona da seguinte forma:
            <ul>
              <li>
                Varivel referenciada  <strong>procurada no escopo atual</strong>
              </li>
              <li>
                <strong>Se encontrou,</strong> a varivel  recuperada e utilizada sem problemas
              </li>
              <li>
                <strong>Se no encontrou,</strong> existem dois cenrios:
              </li>
              <ul>
                <li>
                  <strong>Se o escopo atual  local,</strong> a busca continua e prossegue da mesma forma
                </li>
                <li>
                  <strong>Se o escopo atual  o global e a varivel no foi encontrada,</strong> ela  ento declarada e
                  inicializada sem valor <strong>(undefined)</strong>
                </li>
              </ul>
            </ul>
            Note que  possvel <strong>redefinir e mascarar variveis de mesmo nome</strong> ao defini-las novamente
            em um escopo mais &quot;interno&quot;. Essa prtica  chamada de <strong>shadowing</strong> e&nbsp;
            <strong>deve ser usada com muito cuidado.</strong>
            <H3>Contexto e binding</H3>
            At agora, foi falado sobre <strong>escopos locais e globais</strong> relativos a organizao do cdigo.
            Por padro, o <strong>contexto</strong> para as funes  definido automaticamente como o escopo de
            maior precedncia <strong>(pode ser o global ou o local imediatamente aps o atual)</strong>.
            Podemos ir alm e definir <strong>&quot;contextos manuais&quot;</strong>, aonde funes podem obedecer
            escopos alm do definido originalmente.
            <br /><br />
            Essa mudana de contexto  chamada de <strong>binding</strong>, e  muito utilizada em&nbsp;
            <strong>orientao a objetos e funes executadas em diferentes pontos de uma aplicao.</strong>
            <H3>Strict Mode</H3>
            No ES5, foi criado o <strong>Strict Mode</strong>, responsvel por&nbsp;
            <strong>
              impor limitaes sintticas e exibir erros que normalmente seriam ignorados e corrigidos
              pelo interpretador.&nbsp;
            </strong>
            Conforme visto no <strong>Go-Fish</strong>, se uma varivel referenciada no  encontrada, ela  ento
            &quot;criada&quot; pelo interpretador e recebe o valor <strong>undefined</strong>.&nbsp;
            O Strict Mode  comumente usado para &quot;cancelar&quot; esse procedimento,
            retornando erros de variveis indefinidas.
            <br /><br />
            Devido a problemas de compatibilidade e limitaes tcnicas, esse modo  considerado&nbsp;
            <strong>antipadro e deve ser evitado</strong>. Em verses posteriores do ES, foram adicionadas&nbsp;
            <strong>novas estruturas e sintaxes</strong> que se comportam de maneira similar a esse modo.
          </CodeComment>
          <CodeFrame
            title="basics/9_scope.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/16qd5nsw/embedded/js,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <Accordion title="2.10. Manipulao do DOM">
        <CodeArea>
          <CodeComment>
            Talvez a principal funcionalidade, aquela que motivou at mesmo a criao da linguagem,  a&nbsp;
            <strong>manipulao de elementos renderizados no DOM (Document Object Model)</strong>.  possvel editar
            praticamente <strong>todos</strong> os atributos de uma pgina HTML e seus elementos, atravs do objeto
            global <strong>document</strong>.
            <br /><br />
            O uso mais comum  o de <strong>recuperar e modificar</strong> estilos e contedos de determinados
            elementos, utilizando&nbsp;
            <strong>
              id, classe ou at mesmo&nbsp;
              <a href="https://developer.mozilla.org/en-US/docs/Learn/CSS/Introduction_to_CSS/Selectors">
                seletores CSS
              </a>
            </strong>.&nbsp;
            H, tambm, suporte para&nbsp;
            <a href="https://developer.mozilla.org/pt-BR/docs/Web/API/Element/addEventListener">
              event listeners
            </a>&nbsp;
            e interao com os objetos <strong>window, location e history</strong>.
            <br /><br />
            Em frameworks como <a href="https://reactjs.org/">React</a> e <a href="https://vuejs.org/">Vue</a> a
            organizao dos elementos  definida pelo prprio cdigo Javascript. Eles so inseridos na pgina&nbsp;
            <strong>durante a execuo</strong> do script atravs de um&nbsp;
            <a href="http://reactkungfu.com/2015/10/the-difference-between-virtual-dom-and-dom/">Virtual DOM</a>.
          </CodeComment>
          <CodeFrame
            title="basics/10_dom.js"
            width="100%"
            height="auto"
            src="//jsfiddle.net/santiagogustavo/qpakmz0c/3/embedded/js,html,result/dark/"
            frameBorder="0"
          />
        </CodeArea>
      </Accordion>
      <NavLinks>
        <PrevLink to="/1-wtf-is-js">1. WTF is JS</PrevLink>
        <NextLink to="/3-es6">3. ES6</NextLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default Basics;



